<template>
	<main class="zxhwtop50">
		<zxhwheader @routechange="change" :nowtab="'rexiao'"></zxhwheader>
		<section>
			<div class="title">
				<h1>热销·卡友一周最爱Top50</h1>
				<hr>
			</div>
			<div class="content">
				<ul
				    v-waterfall-lower="loadMore"
				    waterfall-disabled="disabled"
				    waterfall-offset="0">
				  <li v-for="(item,index) in list">
				  	<div class="image">
				  		<img :src="item.image" alt="">
				  		<p><img src="../../assets/zxhw/icon_privilege@2x.png" alt=""><span>Top{{index+1}}</span></p>
				  	</div>
					<div class="content">
						<p><img class="avater" :src="item.avater" alt=""><span>{{item.uname}}</span></p>
						<h3>{{item.name}}</h3>
						<h2>{{item.desp}}</h2>
						<ul>
							<li><span class="pay">￥{{item.pay}}</span><span class="paybefore">￥{{item.paybefore}}</span></li>
							<li>详情</li>
						</ul>
					</div>
				  </li>
				</ul>
			</div>
		</section>
		<zxhwfooter :active='1'></zxhwfooter>		
	</main>
</template>
<script>
import "lib-flexible"
import { Waterfall,Dialog } from 'vant';
import zxhwheader from '../../components/zxhwheader.vue'
import zxhwfooter from '../../components/zxhwfooter.vue'
export default{
	name:'zxhwtop50',
	data(){
		return{
			//瀑布流模拟数据 仅供参考
			list: [{'name':'荣获亚洲最具影响力设计大奖的碗','image':require('../../assets/zxhw/produce.png'),'avater':require('../../assets/zxhw/avater.png'),'uname':'严选好品','desp':'可随水位的不同，呈现不同的月亮形状','pay':'85.00','paybefore':'99.00','address':'0'},{'name':'荣获亚洲最具影响力设计大奖的碗','image':require('../../assets/zxhw/produce.png'),'avater':require('../../assets/zxhw/avater.png'),'uname':'严选好品','desp':'可随水位的不同，呈现不同的月亮形状','pay':'85.00','paybefore':'99.00','address':'0'},{'name':'荣获亚洲最具影响力设计大奖的碗','image':require('../../assets/zxhw/produce.png'),'avater':require('../../assets/zxhw/avater.png'),'uname':'严选好品','desp':'可随水位的不同，呈现不同的月亮形状','pay':'85.00','paybefore':'99.00','address':'0'},{'name':'荣获亚洲最具影响力设计大奖的碗','image':require('../../assets/zxhw/produce.png'),'avater':require('../../assets/zxhw/avater.png'),'uname':'严选好品','desp':'可随水位的不同，呈现不同的月亮形状','pay':'85.00','paybefore':'99.00','address':'0'}],
			disabled: false
		}
	},
	directives: {
	    WaterfallLower: Waterfall('lower')
	},
	methods:{
		change(val){
			this.$router.push({'name':val});
		},
		loadMore() {
		  //这里进行瀑布流下拉无限加载更多的接口操作
	      if(this.list.length<50){
		      this.disabled = true;
		      setTimeout(() => {
		        for (let i = 0; i < 5; i ++) {
		          this.list.push({'name':'荣获亚洲最具影响力设计大奖的碗','image':require('../../assets/zxhw/produce.png'),'avater':require('../../assets/zxhw/avater.png'),'uname':'严选好品','desp':'可随水位的不同，呈现不同的月亮形状','pay':'85.00','paybefore':'99.00','address':'0'});
		          if(this.list.length==50){
		          	return false;
		          }
		        }
		        this.disabled = false;
		      }, 200);
	      }
	    }
	},
	components:{
		zxhwheader,
		zxhwfooter
	}
}
</script>
<style lang="less">
main.zxhwtop50{
	width: 100%;
	padding-top: 160px;
	background-color: #fff;
	>section{
		background-color: #fff;
		padding-bottom: 100px;
		>div.title{
			position: relative;
			padding:30px 0;
			>hr{
				background-color: #ddd;
				border:none;
				height: 2px;
			}
			>h1{
				// width: 5.373333rem;
				font-family: 'PingFang-SC-Medium';
				font-weight: bold;
				position: absolute;
				top:40%;
				left:14%;
				width: 7.5rem;
				text-align: center;
				background-color: #fff;	
				font-size: 36px;
				color:#000;
			}
		}
		>div.content{
			width: auto;
			margin:0 30px;
			>ul{
				>li{
					&:first-child{
						margin-top:82px;
					}
					margin-top:40px;
					padding-bottom: 34px;
					border-bottom: 1px solid #ddd;
					>div.image{
						width:100%;
						height:440px;
						position: relative;
						>img{
							width:100%;
							display: block;
							height:440px;
						}
						>p{
							margin:0;
							position: absolute;
							top:0;
							right:0;
							width: 132px;
							height: 56px;
							line-height: 56px;
							text-align: center;
							background-image: linear-gradient(0deg, #bf1c35 0%, #fc7480 100%), linear-gradient(#ffffff, #ffffff);
							border-radius: 0px 5px 0px 5px;
							>img{
								width: 25px;
								height: 25px;
								position: relative;
								top:-0.2rem;
								margin-right: 10px;
							}
							>span{
								font-size: 28px;
								color:#fff;
								position: relative;
								top:-0.3rem;
							}
						}
					}
					>div.content{
						>p{
							margin:20px 0;
							>img{
								width:60px;
								height:60px;
								margin-right: 20px;
							}
							>span{
								color:#919191;
								font-size: 30px;
								position: relative;
								top:-0.1rem;
							}
						}
						>h2{
							font-size: 30px;
							color:#919191;
						}
						>h3{
							font-size: 36px;
							margin-bottom: 25px;
						}
						>ul{
							display: flex;
							align-items:center;
							margin-top: 10px; 
						    justify-content: space-between;
						    >li{
						    	&:first-child{
						    		position: relative;
						    		font-size: 30px;
						    		>span{
						    			&.pay{
											font-size: 32px;
											color:#f5401e;
						    			}
						    			&.paybefore{
											font-size: 30px;
											color:#777;
						    			}
						    		}
						    	}
						    	&:last-child{
						    		font-size: 24px;
						    		color:#919191;
						    		text-align: center;
						    		width: 128px;
						    		line-height: 47px;
									height: 47px;
									border-radius: 5px;
									border: solid 1px #bbbbbb;
						    	}
						    }
						}
					}
				}
			}
		}
	}
}	
</style>


